import axios from 'axios'
import store from '../store'
// axios.defaults.withCredentials = true;//true
import {
    Guid
} from './guid'
let deviceid = null
let id = window.localStorage.getItem('deviceid')
if (id) {
    deviceid = id
} else {
    id = Guid.NewGuid.toString('D')
}
// export default instance;
// instance axios instance
const instance = axios.create({
    // baseURL: 'http://120.53.31.103:84/api/app/',
    baseURL:'https://www.365msmk.com/api/app/',
    timeout: "7000"
})
//ajaxcookie
// instance.defaults.withCredentials=true;
// 
instance.interceptors.request.use((config) => {
    const token = localStorage.getItem('token')
    if (token) { // tokenhttp headertoken
        config.headers.authorization = `Bearer  ${token}` //token
    }
    store.state.isshow = true
    config.headers.deviceid = deviceid
    config.headers.devicetype = 'h5'
    //  store.state.islod=true
    return config
})
// 
instance.interceptors.response.use((res) => {
    //  if(res.data.meta.status == 401){
    //      window.sessionStorage.removeItem('token')
    //      router.push('/login')
    //  }
    store.state.isshow = false
    return res
})
// axios
export default instance